/* ===   === */
.signup-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 100px);
  padding: 60px 20px;
  /*  ,    */
  background-color: #000000;
  background-image: repeating-linear-gradient(
      0deg,
      rgba(255, 255, 255, 0.02) 0px,
      transparent 1px,
      transparent 2px,
      rgba(255, 255, 255, 0.02) 3px
    ),
    repeating-linear-gradient(
      90deg,
      rgba(100, 100, 100, 0.02) 0px,
      transparent 1px,
      transparent 2px,
      rgba(100, 100, 100, 0.02) 3px
    );
}

/* ===   === */
.signup-box {
  width: 100%;
  max-width: 500px;
  /*     */
  background-color: rgba(10, 10, 10, 0.95);
  padding: 50px 45px;
  border-radius: 0; /*   */
  border: 2px solid #ffffff;
  /* box-shadow: 0 0 40px rgba(255, 255, 255, 0.3); <-    */
  /*   */
  font-family: "Pretendard", sans-serif;
}

/* ===  === */
.signup-title {
  text-align: center;
  font-size: 2rem; /*   */
  font-weight: 900;
  color: #ffffff;
  margin-bottom: 40px;
  letter-spacing: 2px;
  text-transform: uppercase;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.signup-form {
  display: flex;
  flex-direction: column;
  gap: 20px; /*   */
}

/* ===   === */
.signup-input {
  width: 100%;
  padding: 18px 20px; /*   */
  font-size: 16px;
  /* , ,    */
  background-color: rgba(30, 30, 30, 0.8);
  color: #ffffff;
  border: 1px solid #555;
  border-radius: 0;
  outline: none;
  transition: all 0.3s ease;
  box-sizing: border-box;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.signup-input:focus {
  border-color: #ff3b30; /*     */
  /* box-shadow: 0 0 8px rgba(255, 59, 48, 0.5); <-    */
}

.signup-input::placeholder {
  color: #666;
}

/* ===   === */
.signup-error {
  color: #ff3b30; /*    */
  font-size: 13px;
  font-weight: 600;
  margin-top: -8px;
  margin-bottom: 5px;
  padding-left: 5px;
  letter-spacing: 0.5px;
}

/* ===   === */
.signup-submit-btn {
  width: 100%;
  padding: 18px;
  /* , ,   */
  background-color: #ffffff;
  color: #000000;
  border: 2px solid #ffffff;
  border-radius: 0;
  cursor: pointer;
  font-size: 16px;
  font-weight: 900;
  margin-top: 15px;
  transition: all 0.3s ease;
  box-sizing: border-box;
  letter-spacing: 2px;
  text-transform: uppercase;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.signup-submit-btn:hover {
  background-color: #ff3b30; /*     */
  color: #ffffff;
  border-color: #ff3b30;
  /* box-shadow: 0 0 15px rgba(255, 59, 48, 0.8); <-    */
  transform: translateY(-2px);
}

.signup-submit-btn:disabled {
  background-color: #333;
  color: #666;
  border-color: #555;
  cursor: not-allowed;
  transform: none;
}

/* ===   === */
.signup-login-link {
  text-align: center;
  margin-top: 30px;
  padding-top: 30px;
  border-top: 1px solid #444;
  color: #999;
  font-size: 14px;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.signup-login-link a {
  color: #ffffff; /*   */
  text-decoration: none;
  font-weight: 600;
  margin-left: 5px;
  transition: color 0.2s ease;
}

.signup-login-link a:hover {
  color: #ff3b30; /*     */
  text-decoration: underline;
}

/* ===   === */
@media (max-width: 768px) {
  .signup-container {
    padding: 40px 15px;
  }

  .signup-box {
    padding: 35px 25px;
    border-width: 1px;
  }

  .signup-title {
    font-size: 1.8rem;
    margin-bottom: 30px;
  }

  .signup-input {
    padding: 15px 16px;
    font-size: 15px;
  }

  .signup-submit-btn {
    padding: 15px;
    font-size: 15px;
    letter-spacing: 1px;
  }

  .signup-error {
    font-size: 12px;
  }
}

.password-rules {
  display: flex;
  margin-top: 8px;
  text-align: left;
  font-size: 14px;
  line-height: 1.6;
}

.password-rules .valid {
  color: #2ecc71; /*  */
  transition: 0.2s;
}

.password-rules .invalid {
  color: #e74c3c; /*  */
  transition: 0.2s;
}
