/* ========================================
      -    (CPU)
   [Pretendard    ]
   ======================================== */

/*    CSS   . */

/* ===   === */
.cpu-page-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  /*  ,    */
  background-color: #000000;
  background-image: repeating-linear-gradient(
      0deg,
      rgba(255, 255, 255, 0.02) 0px,
      transparent 1px,
      transparent 2px,
      rgba(255, 255, 255, 0.02) 3px
    ),
    repeating-linear-gradient(
      90deg,
      rgba(100, 100, 100, 0.02) 0px,
      transparent 1px,
      transparent 2px,
      rgba(100, 100, 100, 0.02) 3px
    );
  min-height: 100vh;
  padding: 40px 40px 40px 0; /*    */
  gap: 32px;
}

/*   -    . */
.cpu-view-content {
  flex: 1;
  padding-left: 40px; /* ComputerSidebar    */
}

/* ===  &  === */
.loading-container,
.error-container {
  max-width: 1000px;
  margin: 0 auto;
  text-align: center;
  padding: 80px 20px;
  background: rgba(10, 10, 10, 0.95);
  border: 2px solid #ffffff;
  box-shadow: 0 0 40px rgba(255, 255, 255, 0.3);
  /*   */
  font-family: "Pretendard", sans-serif;
  position: relative;
}

/*   */
.loading-container::before,
.error-container::before {
  content: "SYSTEM LOG";
  position: absolute;
  top: 10px;
  left: 10px;
  color: #ff3b30;
  font-size: 10px;
  letter-spacing: 2px;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.loading-spinner {
  width: 60px;
  height: 60px;
  margin: 0 auto 30px;
  border: 4px solid rgba(60, 60, 60, 0.3);
  border-top-color: #ff3b30;
  border-radius: 50%;
  /* animation: spin 1s linear infinite; -    CSS  . */
  box-shadow: 0 0 20px rgba(255, 59, 48, 0.4);
}

.error-message {
  color: #aaa;
  font-size: 16px;
  margin-bottom: 32px;
  letter-spacing: 1px;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.back-btn {
  padding: 14px 40px;
  background: rgba(255, 59, 48, 0.1);
  color: #ffffff;
  border: 2px solid #ff3b30;
  font-size: 13px; /*     */
  font-weight: 900;
  cursor: pointer;
  letter-spacing: 2px;
  text-transform: uppercase;
  transition: all 0.3s;
  /*   */
  font-family: "Pretendard", sans-serif;
  position: relative;
  overflow: hidden;
  border-radius: 0;
}

.back-btn:hover {
  background: #ff3b30;
  transform: translateY(-2px);
  box-shadow: 0 0 20px rgba(255, 59, 48, 0.5);
  border-color: #ffffff;
}

/* ===    === */
.product-detail-container {
  max-width: 1200px;
  /* margin: 0 auto; */
  background: rgba(10, 10, 10, 0.95);
  border: 2px solid #ffffff;
  box-shadow: 0 0 40px rgba(255, 255, 255, 0.3);
  /*   */
  font-family: "Pretendard", sans-serif;
  position: relative;
}

/*   (AI   ) */
.product-detail-container::before,
.product-detail-container::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  border: 3px solid #ff3b30;
}

.product-detail-container::before {
  top: -2px;
  left: -2px;
  border-right: none;
  border-bottom: none;
}

.product-detail-container::after {
  bottom: -2px;
  right: -2px;
  border-left: none;
  border-top: none;
}

/* ===   ( + ) === */
.product-overview {
  display: grid;
  grid-template-columns: 400px 1fr;
  gap: 60px;
  padding-top: 60px;
  padding-right: 60px;
  /* border-bottom: 2px solid #333;   */
}

.product-image-section {
  width: 100%;
  height: 300px;
  aspect-ratio: 1;
  display: flex;
  /* align-items: center; */
  align-items: left;
  /* justify-content: center; */
  justify-content: left;
  /* background: rgba(20, 20, 20, 0.8); */
  /* border: 2px solid #555; */
  /* box-shadow: 0 0 20px rgba(255, 255, 255, 0.1); */
  overflow: hidden;
  position: relative;
  border-radius: 0;
}

.product-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  padding: 0px;
  filter: grayscale(0.5) contrast(1.1);
}

.product-image-placeholder {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  color: #666;
  font-size: 14px;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 2px;
  /*   */
  font-family: "Pretendard", sans-serif;
}

/* ===   === */
.product-info-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.product-category {
  font-size: 12px; /*    */
  color: #ff3b30;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-bottom: 24px;
  text-shadow: 0 0 5px rgba(255, 59, 48, 0.5);
  /*   */
  font-family: "Pretendard", sans-serif;
}

.product-name {
  font-size: 28px; /*   */
  font-weight: 500;
  color: #ffffff;
  margin-bottom: 40px;
  line-height: 1.3;
  letter-spacing: 1px;
  /*   */
  font-family: "Pretendard", sans-serif;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
  /* animation: neonPulse 3s infinite; -    CSS  . */
}

.product-basic-info {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.info-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0;
  border-bottom: 1px dashed #444; /*   */
  transition: all 0.3s;
}

.info-row:hover {
  background: rgba(255, 255, 255, 0.03);
  padding-left: 15px; /*     */
  border-color: #555;
}

.info-label {
  font-size: 11px;
  color: #888;
  font-weight: 800; /*  */
  text-transform: uppercase;
  letter-spacing: 2px;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.info-value {
  font-size: 16px;
  color: #ccc;
  font-weight: 600;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.info-value.price {
  font-size: 28px; /*   */
  font-weight: 500;
  letter-spacing: 2px;
  color: #ffffff;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
  /*   */
  font-family: "Pretendard", sans-serif;
}

/* ===    === */
.product-specs-section {
  padding: 60px;
  border-bottom: 2px solid #333; /*   */
}

.section-title {
  font-size: 12px; /*    */
  color: #ff3b30;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-bottom: 32px;
  text-shadow: 0 0 5px rgba(255, 59, 48, 0.5);
  /*   */
  font-family: "Pretendard", sans-serif;
}

.specs-grid {
  display: grid;
  grid-template-columns: repeat(
    auto-fill,
    minmax(200px, 1fr)
  ); /*    */
  gap: 20px;
}

.spec-item {
  padding: 6px 0;
  border-left: 3px solid #555; /*   */
  padding-left: 10px;
  transition: all 0.3s;
}

.spec-item:hover {
  border-left-color: #ff3b30; /*     */
  background: rgba(255, 59, 48, 0.03); /*    */
  transform: translateX(3px); /*   */
}

.spec-label {
  font-size: 11px;
  color: #888;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 8px;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.spec-value {
  font-size: 15px;
  color: #ffffff; /*    */
  font-weight: 600;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.no-specs {
  color: #666;
  font-size: 14px;
  text-align: center;
  padding: 40px;
  border: 1px dashed #444;
  /*   */
  font-family: "Pretendard", sans-serif;
}

/* ===   === */
.product-actions {
  padding: 40px 60px;
  display: flex;
  gap: 20px; /*   */
  justify-content: flex-end;
  background: rgba(20, 20, 20, 0.8);
  border-top: 2px solid #333; /*   */
}

.action-btn {
  padding: 18px 40px; /*   */
  font-size: 13px;
  font-weight: 900;
  letter-spacing: 2px;
  text-transform: uppercase;
  border: 2px solid #555;
  cursor: pointer;
  transition: all 0.3s;
  text-decoration: none;
  display: inline-block;
  /*   */
  font-family: "Pretendard", sans-serif;
  position: relative;
  overflow: hidden;
  border-radius: 0;
}

/*    */
.btn-list,
.btn-edit,
.btn-delete {
  background: rgba(255, 255, 255, 0.05);
  color: #ffffff; /*    */
  border-color: #555;
}

/*   */
.btn-list:hover {
  background: rgba(255, 255, 255, 0.15);
  border-color: #ffffff;
  color: #ffffff;
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.3);
  transform: translateY(-3px); /*   */
}

/*   */
.btn-edit {
  border-color: #ffffff;
  color: #ffffff;
  background: rgba(255, 255, 255, 0.1);
}

.btn-edit:hover {
  background: #ffffff;
  color: #1a1a1a;
  border-color: #ffffff;
  transform: translateY(-3px);
  box-shadow: 0 0 25px rgba(255, 255, 255, 0.5);
}

/*   (  ) */
.btn-delete {
  border-color: #ff3b30;
  color: #ff3b30;
  background: rgba(255, 59, 48, 0.05);
}

.btn-delete:hover {
  background: #ff3b30;
  color: #000000; /*    */
  border-color: #ff3b30;
  transform: translateY(-3px);
  box-shadow: 0 0 25px rgba(255, 59, 48, 0.8); /*   */
}

/* ===  === */
@media (max-width: 1024px) {
  .cpu-view-content {
    padding-left: 20px; /* ComputerSidebar     */
    padding-right: 20px;
  }

  .product-detail-container {
    margin: 0;
  }

  .product-overview {
    grid-template-columns: 1fr;
    gap: 40px;
    padding: 40px;
  }

  .product-image-section {
    max-width: 400px;
    margin: 0 auto;
  }

  .specs-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .cpu-view-content {
    padding: 40px 20px;
  }

  .product-overview,
  .product-specs-section {
    padding: 32px 24px;
  }

  .product-name {
    font-size: 28px;
    margin-bottom: 30px;
  }

  .product-actions {
    padding: 24px;
    flex-wrap: wrap;
    gap: 12px;
  }

  .action-btn {
    flex: 1;
    min-width: 100px;
    padding: 12px 20px;
    font-size: 11px;
    letter-spacing: 1px;
  }

  .info-value.price {
    font-size: 24px;
  }
}
