/* Sidebar Container */
.computer-sidebar {
  width: 280px; /*      */
  min-height: 50vh;
  padding: 40px 0; /*    */
  background-color: #050505; /*    */
  border-right: 3px solid #ffffff; /*    */
  flex-shrink: 0;
  box-shadow: 15px 0 30px rgba(255, 255, 255, 0.2); /*    */
  position: sticky; /*    */
  top: 0; /*   */
  /*   */
  font-family: "Pretendard", sans-serif;
  z-index: 10;
}

.computer-sidebar::before {
  /* /   */
  content: "";
  position: absolute;
  top: -3px; /* border-width  */
  right: -3px;
  width: 25px; /*   */
  height: 25px;
  border: 3px solid #ff3b30; /*  */
  border-left: none;
  border-bottom: none;
}

.computer-sidebar::after {
  content: "";
  position: absolute;
  bottom: -3px;
  right: -3px;
  width: 25px;
  height: 25px;
  border: 3px solid #ff3b30;
  border-left: none;
  border-top: none;
}

/* Sidebar Title */
.sidebar-title {
  padding: 0 30px 20px; /*   */
  font-size: 16px;
  font-weight: 900;
  color: #ffffff;
  border-bottom: 3px solid #ff3b30; /*     */
  margin-bottom: 30px; /*   */
  text-transform: uppercase;
  letter-spacing: 4px; /*   */
  /*   */
  font-family: "Pretendard", sans-serif;
  /* animation: neonPulse 3s infinite; */
  position: relative;
}

/* Sidebar Menu */
.sidebar-menu {
  padding: 0 0px;
}

/* Menu Item Link */
.sidebar-menu-item {
  display: flex;
  align-items: center;
  padding: 16px 30px; /*   */
  text-decoration: none;
  color: #aaaaaa;
  font-size: 16px;
  font-weight: 600;
  transition: all 0.3s;
  border-left: 4px solid transparent;
  position: relative;
  overflow: hidden;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.sidebar-menu-item::before {
  /*      */
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 4px;
  height: 100%;
  background: linear-gradient(
    to bottom,
    #ffffff,
    #ff3b30
  ); /*  ->   */
  transform: scaleY(0);
  transform-origin: top;
  transition: transform 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94); /*   */
}

.sidebar-menu-item:hover {
  background-color: rgba(255, 255, 255, 0.08);
  color: #ffffff;
  /* border-left-color: #ff3b30;      */
  box-shadow: inset 5px 0 15px rgba(255, 59, 48, 0.1); /*    */
  transform: translateX(3px); /*   */
}

.sidebar-menu-item:hover::before {
  transform: scaleY(1);
}

.sidebar-menu-item.active, /* 'active'   */
.sidebar-menu-item:focus {
  background-color: rgba(255, 59, 48, 0.15); /*   */
  color: #ff3b30;
  border-left-color: #ff3b30;
  font-weight: 900;
  text-shadow: 0 0 8px rgba(255, 59, 48, 0.8); /*   */
}

/* ===  CSS    === */

@keyframes neonPulse {
  0%,
  100% {
    text-shadow: 0 0 5px #ffffff, 0 0 10px #ffffff, 0 0 15px #ffffff;
  }
  50% {
    text-shadow: 0 0 2px #ffffff, 0 0 5px #ffffff, 0 0 8px #ffffff;
  }
}

@keyframes borderGlow {
  0%,
  100% {
    box-shadow: 0 0 5px #ff3b30, 0 0 10px #ff3b30, inset 0 0 5px #ff3b30;
  }
  50% {
    box-shadow: 0 0 10px #ff3b30, 0 0 20px #ff3b30, inset 0 0 10px #ff3b30;
  }
}

/* ===  (    ) === */
@media (max-width: 1024px) {
  .computer-sidebar {
    width: 100%; /*     */
    min-height: auto;
    padding: 20px 0;
    border-right: none;
    border-bottom: 3px solid #ffffff; /*    */
    position: static;
    box-shadow: 0 10px 30px rgba(255, 255, 255, 0.1);
  }

  .computer-sidebar::before,
  .computer-sidebar::after {
    display: none; /*     */
  }

  .sidebar-title {
    text-align: center;
    margin-bottom: 15px;
    padding-bottom: 15px;
  }

  .sidebar-menu {
    display: flex;
    overflow-x: auto; /*     */
    white-space: nowrap;
    padding: 0 10px;
  }

  .sidebar-menu-item {
    flex: 0 0 auto; /*     */
    padding: 10px 15px;
    border-left: none;
    border-bottom: 3px solid transparent;
    transform: none !important;
  }

  .sidebar-menu-item:hover,
  .sidebar-menu-item.active {
    border-left: none;
    border-bottom: 3px solid #ff3b30;
    background-color: rgba(255, 255, 255, 0.05);
    box-shadow: none;
    transform: none !important;
  }

  .sidebar-menu-item::before {
    display: none;
  }
}
