/* ========================================
      - 1:1  
   ======================================== */

.help-container {
  max-width: 1400px;
  margin: 50px auto;
  padding: 0 50px;
  font-family: "Pretendard", sans-serif;
  color: #ffffff;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 50px;
  min-height: calc(100vh - 200px);
}

/* 1:1    */
.help-form-section {
  background: #0a0a0a;
  border: 2px solid #ff3b30;
  padding: 40px;
  border-radius: 0;
}

.help-form-title {
  font-size: 28px;
  font-weight: 900;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #ffffff;
  margin-bottom: 30px;
  padding-bottom: 15px;
  border-bottom: 3px solid #ff3b30;
}

.help-form {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.form-label {
  font-size: 14px;
  font-weight: 800;
  letter-spacing: 1px;
  color: #ffffff;
  text-transform: uppercase;
}

.form-label::after {
  content: " *";
  color: #ff3b30;
}

.form-input {
  padding: 14px 18px;
  background: #1a1a1a;
  border: 2px solid #333;
  color: #ffffff;
  font-size: 15px;
  font-weight: 500;
  transition: all 0.2s;
  border-radius: 0;
  font-family: "Pretendard", sans-serif;
}

.form-input::placeholder {
  color: #666;
  font-weight: 400;
}

.form-input:focus {
  outline: none;
  border-color: #ff3b30;
  background: #111111;
}

.form-textarea {
  padding: 14px 18px;
  background: #1a1a1a;
  border: 2px solid #333;
  color: #ffffff;
  font-size: 15px;
  font-weight: 500;
  transition: all 0.2s;
  border-radius: 0;
  min-height: 200px;
  resize: vertical;
  font-family: "Pretendard", sans-serif;
}

.form-textarea::placeholder {
  color: #666;
  font-weight: 400;
}

.form-textarea:focus {
  outline: none;
  border-color: #ff3b30;
  background: #111111;
}

.submit-btn {
  margin-top: 20px;
  padding: 16px 40px;
  background: transparent;
  border: 2px solid #ff3b30;
  color: #ffffff;
  font-size: 15px;
  font-weight: 900;
  letter-spacing: 2px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.2s;
  border-radius: 0;
  font-family: "Pretendard", sans-serif;
}

.submit-btn:hover {
  background: #ff3b30;
  color: #000000;
  transform: translateY(-2px);
}

.submit-btn:active {
  transform: translateY(0);
}

/*   */
.help-info-section {
  background: #0a0a0a;
  border: 2px solid #333;
  padding: 40px;
  border-radius: 0;
  height: fit-content;
  position: sticky;
  top: 160px;
}

.help-info-title {
  font-size: 22px;
  font-weight: 900;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #ffffff;
  margin-bottom: 25px;
  padding-bottom: 15px;
  border-bottom: 2px solid #333;
}

.help-info-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.help-info-list li {
  padding-left: 20px;
  position: relative;
  font-size: 14px;
  line-height: 1.7;
  color: #aaaaaa;
  font-weight: 500;
}

.help-info-list li::before {
  content: "";
  position: absolute;
  left: 0;
  color: #ff3b30;
  font-weight: 900;
}

.help-info-divider {
  height: 1px;
  background: #333;
  margin: 20px 0;
}

.help-info-hours {
  padding: 15px;
  background: #111111;
  border-left: 4px solid #ff3b30;
  font-size: 13px;
  color: #ffffff;
  font-weight: 700;
  letter-spacing: 0.5px;
  margin-top: 20px;
}

/*   */
@media (max-width: 1024px) {
  .help-container {
    grid-template-columns: 1fr;
    gap: 30px;
    padding: 0 30px;
  }

  .help-info-section {
    position: static;
  }
}

@media (max-width: 768px) {
  .help-container {
    margin: 30px auto;
    padding: 0 20px;
  }

  .help-form-section,
  .help-info-section {
    padding: 25px;
  }

  .help-form-title {
    font-size: 22px;
  }

  .help-info-title {
    font-size: 18px;
  }

  .form-input,
  .form-textarea {
    font-size: 14px;
  }

  .submit-btn {
    padding: 14px 30px;
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .help-container {
    margin: 20px auto;
    padding: 0 15px;
  }

  .help-form-section,
  .help-info-section {
    padding: 20px;
  }

  .help-form-title {
    font-size: 20px;
  }

  .help-info-list li {
    font-size: 13px;
  }
}

/*  Help.css   */

/*   */
.error-text {
