/*    */
.help-list-container {
  max-width: 1200px;
  margin: 50px auto;
  padding: 0 50px;
  font-family: "Pretendard", sans-serif;
  color: #ffffff;
  min-height: calc(100vh - 200px);
}

.help-list-container.admin {
  max-width: 1400px;
}

/*   */
.help-list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 3px solid #ff3b30;
}

.help-list-header h2 {
  font-size: 28px;
  font-weight: 900;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #ff3b30;
}

.new-help-btn {
  padding: 12px 24px;
  background: transparent;
  border: 2px solid #ff3b30;
  color: #ff3b30;
  font-size: 14px;
  font-weight: 900;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.2s;
  border-radius: 0;
  font-family: "Pretendard", sans-serif;
}

.new-help-btn:hover {
  background: #ff3b30;
  color: #000000;
}

/*   */
.filter-buttons {
  display: flex;
  gap: 10px;
}

.filter-buttons button {
  padding: 10px 20px;
  background: #1a1a1a;
  border: 2px solid #333;
  color: #aaaaaa;
  font-size: 13px;
  font-weight: 800;
  cursor: pointer;
  transition: all 0.2s;
  border-radius: 0;
  font-family: "Pretendard", sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.filter-buttons button:hover,
.filter-buttons button.active {
  background: #ff3b30;
  border-color: #ff3b30;
  color: #000000;
}

/*   */
.empty-list {
  background: #0a0a0a;
  border: 2px solid #333;
  padding: 60px 40px;
  text-align: center;
  border-radius: 0;
}

.empty-list p {
  font-size: 16px;
  color: #aaaaaa;
  margin-bottom: 20px;
}

.empty-list button {
  padding: 12px 30px;
  background: transparent;
  border: 2px solid #ff3b30;
  color: #ff3b30;
  font-size: 14px;
  font-weight: 900;
  cursor: pointer;
  transition: all 0.2s;
  border-radius: 0;
  font-family: "Pretendard", sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.empty-list button:hover {
  background: #ff3b30;
  color: #000000;
}

/*   */
.help-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/*   */
.help-item {
  background: #0a0a0a;
  border: 2px solid #333;
  padding: 30px;
  transition: all 0.2s;
  border-radius: 0;
}

.help-item:hover {
  border-color: #ff3b30;
}

.help-item.admin {
  border-left: 4px solid #ff3b30;
}

.help-item-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
}

.help-item-header h3 {
  font-size: 20px;
  font-weight: 900;
  color: #ffffff;
  letter-spacing: 1px;
  margin-bottom: 5px;
}

.author-info {
  font-size: 13px;
  color: #aaaaaa;
  font-weight: 600;
}

/*   */
.status-badge {
  padding: 6px 14px;
  font-size: 12px;
  font-weight: 900;
  letter-spacing: 1px;
  text-transform: uppercase;
  border-radius: 0;
  white-space: nowrap;
}

.status-badge.pending {
  background: #333;
  color: #ff3b30;
  border: 2px solid #ff3b30;
}

.status-badge.answered {
  background: #ff3b30;
  color: #000000;
  border: 2px solid #ff3b30;
}

/*   */
.help-item-content {
  margin-bottom: 20px;
  padding: 15px 0;
  border-top: 1px dashed #333;
  border-bottom: 1px dashed #333;
}

.help-item-content p {
  color: #cccccc;
  line-height: 1.7;
  font-size: 15px;
  white-space: pre-wrap;
}

/*   */
.guest-info {
  display: flex;
  gap: 20px;
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px dashed #444;
}

.guest-info span {
  font-size: 13px;
  color: #888;
  font-weight: 600;
}

/*   */
.help-item-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.help-date {
  font-size: 13px;
  color: #888;
  font-weight: 600;
  letter-spacing: 0.5px;
}

/*   */
.admin-actions {
  display: flex;
  gap: 10px;
}

.delete-btn,
.answer-btn {
  padding: 8px 16px;
  font-size: 13px;
  font-weight: 900;
  letter-spacing: 1px;
  cursor: pointer;
  transition: all 0.2s;
  border-radius: 0;
  font-family: "Pretendard", sans-serif;
  text-transform: uppercase;
}

.delete-btn {
  background: transparent;
  border: 2px solid #666;
  color: #666;
}

.delete-btn:hover {
  background: #666;
  color: #ffffff;
}

.answer-btn {
  background: transparent;
  border: 2px solid #ff3b30;
  color: #ff3b30;
}

.answer-btn:hover {
  background: #ff3b30;
  color: #000000;
}

.answer-btn.answered {
  background: #ff3b30;
  color: #000000;
}

.answer-btn.answered:hover {
  background: transparent;
  color: #ff3b30;
}

