  background: transparent;
  border: 2px solid #666;
  color: #666;
  font-size: 14px;
  font-weight: 900;
  letter-spacing: 1px;
  cursor: pointer;
  transition: all 0.2s;
  border-radius: 0;
  font-family: "Pretendard", sans-serif;
  text-transform: uppercase;
}

.back-btn:hover {
