}

.help-detail-info {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.info-row {
  display: flex;
  gap: 15px;
  align-items: center;
}

.info-label {
  font-size: 14px;
  font-weight: 800;
  color: #aaaaaa;
  min-width: 100px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.info-value {
  font-size: 14px;
  color: #ffffff;
  font-weight: 600;
}

.help-detail-actions {
  margin-top: 30px;
