}

/*      */
.help-item.clickable {
  cursor: pointer;
}

.help-item.clickable:hover {
  border-color: #ff3b30;
  transform: translateY(-2px);
}

.help-item-preview {
  margin-bottom: 15px;
  padding: 15px 0;
  border-top: 1px dashed #333;
}

