  margin-top: 30px;
}

.help-list-section h3 {
  font-size: 20px;
  font-weight: 900;
  color: #ff3b30;
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/*  */
@media (max-width: 1024px) {
  .help-list-container,
  .guest-inquiry-container {
    padding: 0 30px;
  }

  .help-list-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }

  .filter-buttons {
    width: 100%;
  }

  .filter-buttons button {
    flex: 1;
  }
}

@media (max-width: 768px) {
  .help-list-container,
  .guest-inquiry-container,
  .help-detail-container {
    margin: 30px auto;
    padding: 0 20px;
  }

  .help-item {
    padding: 20px;
  }

  .help-item-header {
    flex-direction: column;
    gap: 10px;
  }

  .help-item-footer {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }

  .admin-actions {
    width: 100%;
  }

  .admin-actions button {
    flex: 1;
  }

  .guest-info {
    flex-direction: column;
    gap: 5px;
  }

  .help-detail-card {
    padding: 25px;
  }

  .help-detail-title-section {
    flex-direction: column;
    gap: 15px;
  }

  .help-detail-title-section h2 {
    font-size: 22px;
  }

  .info-row {
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
  }

  .info-label {
    min-width: auto;
  }
}

/*   */
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 40px;
  padding: 20px 0;
}

.page-btn {
  padding: 10px 20px;
  background: transparent;
  border: 2px solid #666;
  color: #666;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.2s;
  font-family: "Pretendard", sans-serif;
}

.page-btn:hover:not(:disabled) {
  background: #666;
  color: #ffffff;
}

.page-btn:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

.page-numbers {
  display: flex;
  gap: 5px;
}

.page-number {
  width: 40px;
  height: 40px;
  background: transparent;
  border: 2px solid #333;
  color: #ffffff;
  font-size: 14px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s;
  font-family: "Pretendard", sans-serif;
}

.page-number:hover {
  background: #333;
  border-color: #ff3b30;
}

.page-number.active {
  background: #ff3b30;
  border-color: #ff3b30;
  color: #000000;
}

@media (max-width: 768px) {
  .pagination {
    flex-wrap: wrap;
  }

  .page-numbers {
    order: 3;
    width: 100%;
    justify-content: center;
    margin-top: 10px;
  }
}
