/* ========================================
      - CPU  
   [ &    ]
   ======================================== */

/*   */
.page-container {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  /* : #1a1a1a ->    */
  background-color: #000000;
  min-height: 100vh; /*   100vh  */
  /* ComputerSidebar     0  */
  padding: 40px 40px 40px 0; /*    */
  gap: 32px; /*   */

  max-width: 2000px;
}

/*   -    */
.product-list {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 20px; /*   */
  /* ComputerSidebar  ,      */
  padding-left: 40px;

  max-width: 1200px;
}

/*    -      */
.cart-panel {
  width: 320px; /*   */
  /* : #2a2a2a ->    */
  background-color: rgba(10, 10, 10, 0.95);
  border-radius: 0; /*   */
  border: 2px solid #ffffff; /*   */
  padding: 20px;
  color: #ffffff;
  position: sticky;
  top: 40px; /*      */
  height: fit-content;
  /* margin-right: 40px;  */
  /*      */
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.3);
}

/*    */
.product-card {
  display: grid;
  grid-template-columns: 100px 1fr 140px 100px; /*    */
  align-items: center;
  /* : #fff, #d1d1d1 ->    */
  background-color: rgba(20, 20, 20, 0.5);
  border: 2px solid rgba(100, 100, 100, 0.4);
  border-radius: 0;
  padding: 18px 20px;
  transition: all 0.3s;
  cursor: pointer;
}

.product-card:hover {
  /* : #ff5c00 ->    */
  border-color: #ffffff;
  background-color: rgba(255, 255, 255, 0.05);
  /*    */
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.3);
}

/*  */
.product-card img {
  width: 80px;
  height: 80px;
  object-fit: contain;
  /*      */
  background: rgba(20, 20, 20, 0.8);
  border: 2px solid #555;
  padding: 8px;
  filter: grayscale(0.2) contrast(1.1);
}

/*  */
.product-card h4 {
  font-size: 16px;
  /* : #111 ->    */
  color: #ccc;
  font-weight: 600;
  margin: 0;
  padding-left: 20px;
  /*   */
  font-family: "Pretendard", sans-serif;
}

/*  +    */
.product-price-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 8px;
  width: 100%; /*    */
}

/*  */
.product-card p {
  /* : #e60012 ->  /  */
  color: #ffffff;
  font-weight: 900;
  font-size: 18px;
  margin: 0;
  letter-spacing: 1px;
  /*   */
  font-family: "Pretendard", sans-serif;
  text-shadow: 0 0 8px rgba(255, 255, 255, 0.5);
}

/*   */
.product-card button {
  /* : #ff5c00 ->  /  */
  background-color: #ff3b30;
  color: #000000;
  border: 2px solid #ff3b30;
  border-radius: 0;
  padding: 8px 12px;
  font-size: 14px;
  font-weight: 900;
  transition: all 0.3s;
  width: 90px;
  letter-spacing: 1px;
  text-transform: uppercase;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.product-card button:hover {
  /* : #e34a00 ->    */
  background-color: #ffffff;
  color: #000000;
  border-color: #ffffff;
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.8);
  transform: scale(1.05);
}

/*    () */
.cart-panel h3 {
  font-size: 18px;
  margin-bottom: 15px;
  /* : #ff5c00 ->    */
  color: #ff3b30;
  border-bottom: 2px solid #444;
  padding-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 1px;
  /*   */
  font-family: "Pretendard", sans-serif;
}

/*    */
.cart-item {
  margin-bottom: 15px;
  /* : #3b3b3b ->    */
  background: rgba(30, 30, 30, 0.8);
  border-radius: 0;
  border-left: 4px solid #ffffff;
  padding: 10px;
  transition: all 0.2s;
}

.cart-item:hover {
  border-left-color: #ff3b30;
  box-shadow: 0 0 10px rgba(255, 59, 48, 0.2);
}

.cart-category {
  font-weight: 800;
  font-size: 13px;
  /* : #ffcc00 ->  /  */
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.cart-name {
  font-size: 14px;
  color: #bbb;
  margin-top: 4px;
  line-height: 1.4;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.cart-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
  margin-top: 8px;
}

.cart-remove {
  background: none;
  border: none;
  /* : #ff4d4d ->    */
  color: #ff3b30;
  font-size: 18px;
  cursor: pointer;
  font-weight: 900;
  transition: transform 0.2s;
}

.cart-remove:hover {
  transform: scale(1.1);
}

.empty-slot {
  color: #888;
  font-size: 12px;
  margin-top: 5px;
  font-family: "Pretendard", sans-serif;
}

.cart-total {
  margin-top: 25px;
  padding-top: 10px;
  border-top: 2px dashed #444;
  text-align: right;
  font-weight: 800;
  font-size: 18px;
  color: #ffffff;
  letter-spacing: 1px;
  /*   */
  font-family: "Pretendard", sans-serif;
  text-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

/*        */
.write-btn-container {
  display: flex;
  justify-content: flex-end;
  margin-top: 30px;
  margin-right: 0;
}

/*    -     */
.write-btn {
  /* : #ffffff, #000000 ->    */
  background-color: #ff3b30;
  color: #000000;
  border: 2px solid #ff3b30;
  border-radius: 0;
  padding: 10px 20px;
  font-size: 15px;
  font-weight: 900;
  cursor: pointer;
  transition: all 0.3s;
  letter-spacing: 2px;
  text-transform: uppercase;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.write-btn:hover {
  background-color: #ffffff;
  border-color: #ffffff;
  transform: translateY(-2px);
  box-shadow: 0 4px 20px rgba(255, 255, 255, 0.5);
}

/*      */
.product-list .product-card button,
.write-btn {
  min-width: 90px;
  text-align: center;
}

.category-title {
  font-size: 24px; /*   */
  font-weight: 900;
  color: #ffffff;
  margin-bottom: 20px;
  padding: 10px 15px;
  border-left: 5px solid #ff3b30; /*   */
  background-color: rgba(15, 15, 15, 0.8);
  border-radius: 0;
  /*    */
  box-shadow: 0 0 15px rgba(255, 59, 48, 0.4);
  text-transform: uppercase;
  letter-spacing: 2px;
  /*   */
  font-family: "Pretendard", sans-serif;
}

/*   */
.search-container {
  display: flex;
  gap: 12px;
  margin-bottom: 25px;
  /* : #2a2a2a ->    */
  background-color: rgba(15, 15, 15, 0.9);
  padding: 15px;
  border-radius: 0;
  border: 1px solid #444;
}

.search-input {
  flex: 1;
  padding: 12px 16px;
  border: 2px solid #555;
  border-radius: 0;
  font-size: 15px;
  /* : #1a1a1a ->    */
  background-color: rgba(20, 20, 20, 0.8);
  color: #fff;
  transition: all 0.3s;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.search-input:focus {
  outline: none;
  /* : #ff5c00 ->    */
  border-color: #ffffff;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.search-input::placeholder {
  color: #666;
}

.search-btn {
  /* : #ff5c00 ->    */
  background-color: #ff3b30;
  color: #000000;
  border: 2px solid #ff3b30;
  border-radius: 0;
  padding: 12px 28px;
  font-size: 14px;
  font-weight: 900;
  cursor: pointer;
  transition: all 0.3s;
  letter-spacing: 1px;
  text-transform: uppercase;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.search-btn:hover {
  background-color: #ffffff;
  border-color: #ffffff;
  color: #000000;
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.5);
}

/*  */
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin: 30px 0;
  padding: 18px;
  /* : #2a2a2a ->    */
  background-color: rgba(15, 15, 15, 0.9);
  border-radius: 0;
  border: 1px solid #444;
}

.page-btn {
  /* : #3b3b3b ->    */
  background-color: rgba(30, 30, 30, 0.8);
  color: #ffffff;
  border: 1px solid #666;
  border-radius: 0;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s;
  /*   */
  font-family: "Pretendard", sans-serif;
}

.page-btn:hover:not(:disabled) {
  /* : #ff5c00 ->    */
  background-color: #ff3b30;
  border-color: #ff3b30;
  color: #000000;
  box-shadow: 0 0 10px rgba(255, 59, 48, 0.5);
}

.page-btn:disabled {
  /* : #1a1a1a ->    */
  background-color: rgba(10, 10, 10, 0.8);
  color: #444;
  cursor: not-allowed;
  border-color: #333;
}

.page-info {
  color: #fff;
  font-size: 15px;
  font-weight: 500;
  min-width: 180px;
  text-align: center;
  /*   */
  font-family: "Pretendard", sans-serif;
}

/*  */
@media (max-width: 1024px) {
  .page-container {
    flex-direction: column;
    /*   0   20px  */
    padding: 20px 20px 20px 20px;
    gap: 25px;
  }

  .product-list {
    padding-left: 0; /*     */
    padding-right: 0;
  }

  .cart-panel {
    width: auto;
    margin: 0 0 20px 0;
    position: static;
  }

  .product-card {
    grid-template-columns: 80px 1fr 100px; /* , , /  */
    padding: 15px;
  }

  .product-card img {
    width: 60px;
    height: 60px;
  }

  .product-card h4 {
    padding-left: 10px;
    font-size: 14px;
  }

  .product-price-container {
    flex-direction: row; /*      */
    justify-content: space-between;
    align-items: center;
  }

  .product-card p {
    font-size: 16px;
  }

  .product-card button {
    width: 70px;
    padding: 6px 10px;
    font-size: 13px;
    font-weight: 700;
  }
}
